import moment from 'moment'

const dateFormat = 'YYYY-MM-DD HH:mm:ss'

const splitRecord = (record, interval) => {
  const trueStart = moment(record['Start Time'], dateFormat)
  const trueEnd = moment(record['End Time'], dateFormat)

  let records = []
  let currentStart = trueStart
  while (currentStart.isBefore(trueEnd)) {
    let newStart = currentStart.format(dateFormat)
    let newEnd = currentStart.endOf(interval)
    newEnd = newEnd.isBefore(trueEnd) ? newEnd.format(dateFormat) : trueEnd.format(dateFormat)
    records.push({
      ...record,
      'Start Time': newStart,
      'End Time': newEnd
    })
    currentStart = currentStart.startOf(interval).add(1, interval)
  }

  return [...records]
}

export const splitBy = (records, interval) => records
  .map(r => splitRecord(r, interval))
  .reduce((acc, curr) => [...acc, ...curr], [])
