import moment from 'moment'

export default function (records) {
  let activities = {}
  let totalSum = 0

  for (let i = 0; i < records.length; i++) {
    let r = records[i]
    let curCount = activities[r.Activity] | 0
    let duration = moment.duration(moment(r['End Time']).diff(moment(r['Start Time']))).asMinutes()
    activities[r.Activity] = curCount + duration
    totalSum += duration
  }

  let activitiesKeys = Object.keys(activities)
  for (let i = 0; i < activitiesKeys.length; i++) {
    activities[activitiesKeys[i]] /= totalSum
  }

  return activities
}
