<script>
import { Radar } from 'vue-chartjs'

const fontStyle = {
  fontFamily: 'IBM Plex Mono'
}

export default {
  extends: Radar,
  data: () => ({
    options: {
      responsive: true,
      maintainAspectRatio: false,
      legend: { display: false },
      scale: {
        ticks: { suggestedMin: 0, ...fontStyle },
        pointLabels: { ...fontStyle, fontSize: 16, fontColor: ['#FFA677', '#FFE58D', '#7BE0BD', '#7BC1E0', '#E07BA2'] }
      }
    }
  }),

  props: ['ds'],

  computed: {
    chartdata () {
      let labels = Object.keys(this.ds)
      let data = []
      for (let i = 0; i < labels.length; i++) {
        data.push(Math.round(this.ds[labels[i]] * 100))
      }
      return {
        labels,
        datasets: [{
          label: 'Time spent on each activity',
          data,
          backgroundColor: 'rgba(145, 145, 145, 0.4)',
          borderColor: '#919191'
        }]
      }
    }
  },

  mounted () {
    this.renderChart(this.chartdata, this.options)
  }
}
</script>
