<script>
import { Bar } from 'vue-chartjs'

const fontStyle = {
  fontFamily: 'IBM Plex Mono'
}

export default {
  extends: Bar,
  data: () => ({
    options: {
      responsive: true,
      maintainAspectRatio: false,
      legend: { labels: { ...fontStyle } },
      scales: {
        xAxes: [{
          stacked: true,
          ticks: { ...fontStyle }
        }],
        yAxes: [{
          stacked: true,
          ticks: { ...fontStyle }
        }]
      }
    }
  }),

  props: ['ds'],

  computed: {
    chartdata () {
      const colors = ['#FFA677', '#FFE58D', '#7BE0BD', '#7BC1E0', '#E07BA2']
      const datasets = Object.keys(this.ds).map((k, i) => ({
        label: k,
        backgroundColor: colors[i],
        data: [ ...this.ds[k].map(v => Math.round(v * 100)) ]
      }))
      return {
        labels: ['MON', 'TUE', 'WED', 'THU', 'FRI', 'SAT', 'SUN'],
        datasets
      }
    }
  },

  mounted () {
    this.renderChart(this.chartdata, this.options)
  }
}
</script>
