<template lang="pug">
  p(v-if='loadingData')
    | Loading data...
  div(v-else)
    chart-section(title='Weekdays')
      week-chart(:ds='activitiesByDay')
    chart-section(title='Time spent on each activity')
      activities-radar-chart(:ds='timeByActivity')
    chart-section(title='Hourly activities')
      hourly-radar-chart(:ds='activitiesByHour')
</template>

<script>
import { mapState } from 'vuex'

import ChartSection from '@/components/ChartSection.vue'
import WeekChart from '@/components/WeekChart.vue'
import ActivitiesRadarChart from '@/components/ActivitiesRadarChart.vue'
import HourlyRadarChart from '@/components/HourlyRadarChart.vue'

import timeByActivities from '@/utils/timeByActivities'
import activitiesByDay from '@/utils/activitiesByDay'
import activitiesByHour from '@/utils/activitiesByHour'

export default {
  name: 'home',
  computed: {
    ...mapState({
      activityData: state => state.activitiesList,
      loadingData: state => state.activitiesLoading
    }),
    timeByActivity () {
      return timeByActivities(this.activityData)
    },
    activitiesByDay () {
      return activitiesByDay(this.activityData)
    },
    activitiesByHour () {
      return activitiesByHour(this.activityData)
    }
  },
  components: {
    ChartSection,
    WeekChart,
    ActivitiesRadarChart,
    HourlyRadarChart
  }
}
</script>
