<script>
import { Radar } from 'vue-chartjs'

const fontStyle = {
  fontFamily: 'IBM Plex Mono'
}

export default {
  extends: Radar,
  data: () => ({
    options: {
      responsive: true,
      maintainAspectRatio: false,
      scale: {
        ticks: { suggestedMin: 0, ...fontStyle },
        pointLabels: { ...fontStyle }
      },
      legend: { labels: { ...fontStyle } }
    }
  }),

  props: ['ds'],

  computed: {
    chartdata () {
      const fills = ['rgba(255, 166, 119, 0.4)', 'rgba(255, 229, 141, 0.4)', 'rgba(123, 224, 189, 0.4)', 'rgba(123, 193, 224, 0.4)', 'rgba(224, 123, 162, 0.4)']
      const strokes = ['rgb(255, 166, 119)', 'rgb(255, 229, 141)', 'rgb(123, 224, 189)', 'rgb(123, 193, 224)', 'rgb(224, 123, 162)']
      const datasets = Object.keys(this.ds).map((k, i) => ({
        label: k,
        backgroundColor: fills[i],
        borderColor: strokes[i],
        pointRadius: 0,
        lineTension: 0.2,
        data: [ ...this.ds[k].map(v => Math.round(v * 60)) ]
      }))
      return {
        datasets,
        labels: [
          '00AM',
          '01AM',
          '02AM',
          '03AM',
          '04AM',
          '05AM',
          '06AM',
          '07AM',
          '08AM',
          '09AM',
          '10AM',
          '11AM',
          '12PM',
          '01PM',
          '02PM',
          '03PM',
          '04PM',
          '05PM',
          '06PM',
          '07PM',
          '08PM',
          '09PM',
          '10PM',
          '11PM'
        ]
      }
    }
  },

  mounted () {
    this.renderChart(this.chartdata, this.options)
  }
}
</script>
