<template lang="pug">
  .container
    div(v-if='loadingData')
      | Loading
    table(v-else)
      thead
        tr
          th(v-for='col in columns')
            | {{ col }}
      tbody
        tr(v-for='record in activityData')
          td(v-for='col in columns')
            | {{ record[col] }}

</template>

<script>
import { mapState } from 'vuex'

export default {
  name: 'Data',
  data () {
    return {
      columns: ['Activity', 'Start Time', 'End Time'],
      activities: ['Work', 'Study', 'Class', 'Sleep']
    }
  },
  computed: {
    ...mapState({
      activityData: state => state.activitiesList,
      loadingData: state => state.activitiesLoading
    })
  }
}
</script>

