import moment from 'moment'

import { splitBy } from './recordSplit'

export default function (records) {
  // This will split those activities that span past midnight
  const splitted = splitBy(records, 'day')
  let activities = {}
  let totalsByWeekDay = [ 0, 0, 0, 0, 0, 0, 0 ]

  for (let i = 0; i < splitted.length; i++) {
    let r = splitted[i]
    let actDays = activities[r.Activity] || [ 0, 0, 0, 0, 0, 0, 0 ]
    let weekDay = moment(r['Start Time']).days()
    weekDay = weekDay === 0 ? 6 : weekDay - 1
    let duration = moment.duration(moment(r['End Time']).diff(moment(r['Start Time']))).asMinutes()
    actDays[weekDay] += duration
    activities[r.Activity] = [...actDays]
    totalsByWeekDay[weekDay] += duration
  }

  // Calculate average
  const actKeys = Object.keys(activities)
  // Iterate through activities
  for (let actIdx = 0; actIdx < actKeys.length; actIdx++) {
    const act = actKeys[actIdx]
    // Iterate through week days
    for (let weekDay = 0; weekDay < 7; weekDay++) {
      activities[act][weekDay] /= totalsByWeekDay[weekDay]
    }
  }

  return activities
}
