import moment from 'moment'

import { splitBy } from './recordSplit'

const emptyHourTable = Array(24).fill(0, 0, 23)

export default function (records) {
  // This will split those activities that span more than an hour
  const splitted = splitBy(records, 'hour')
  let activities = {}
  let totalsByHour = [ ...emptyHourTable ]

  for (let i = 0; i < splitted.length; i++) {
    let r = splitted[i]
    let actHours = activities[r.Activity] || [ ...emptyHourTable ]
    let hour = moment(r['Start Time']).hour()
    let duration = moment.duration(moment(r['End Time']).diff(moment(r['Start Time']))).asMinutes()
    actHours[hour] += duration
    activities[r.Activity] = [...actHours]
    totalsByHour[hour] += duration
  }

  // Calculate average
  const actKeys = Object.keys(activities)
  // Iterate through activities
  for (let actIdx = 0; actIdx < actKeys.length; actIdx++) {
    const act = actKeys[actIdx]
    // Iterate through hours
    for (let hour = 0; hour < 23; hour++) {
      activities[act][hour] /= totalsByHour[hour] === 0 ? 1 : totalsByHour[hour]
    }
  }

  return activities
}
